<template>
  <div>
    <v-app-bar
      color="white"
      flat
    >
      <v-app-bar-nav-icon
        class="hidden-md-and-up"
        @click.stop="drawer = !drawer"
      />
      <v-container class="py-0 fill-height">
        <v-avatar width="100">
          <v-img
            contain
            width="200"
            :src="require('@/assets/img/logo.png')"
          />
        </v-avatar>
        <v-btn
          to="/"
          text
          class="hidden-sm-and-down"
        >
          HOME
        </v-btn>
        <v-btn
          text
          class="hidden-sm-and-down"
          to="/customer-page"
        >
          Orderan Saya
        </v-btn>
        <v-btn
          text
          to="/chat"
          class="hidden-sm-and-down"
        >
          Pesan
        </v-btn>
        <v-btn
          text
          class="hidden-sm-and-down"
          to="/profile"
        >
          Profil
        </v-btn>
        <v-btn
          text
          class="hidden-sm-and-down"
          @click="logout"
        >
          logout
        </v-btn>

        <v-spacer />
      </v-container>
    </v-app-bar>
    <v-main class="grey lighten-3">
      <v-card
        flat
        class="mx-auto grey lighten-3"
        max-width="1200"
      >
        <div
          class="app-container"
          :class="{
            'app-mobile': isDevice,
            'app-mobile-dark': theme === 'dark',
          }"
        >
          <!-- <div
            v-if="showOptions"
            class="button-theme"
          >
            <button
              class="button-light"
              @click="theme = 'light'"
            >
              Light
            </button>
            <button
              class="button-dark"
              @click="theme = 'dark'"
            >
              Dark
            </button>
            <button class="button-github">
              <a href="https://github.com/antoine92190/vue-advanced-chat">
                <img src="">
              </a>
            </button>
          </div> -->
          <v-btn
            class="mb-2 elevation-0 hidden-sm-and-down"
            small
            color="primary"
            @click="createRoom()"
          >
            Chat Admin
          </v-btn>
          <chat-container
            v-if="showChat"
            :current-user-id="currentUserId"
            :theme="theme"
            :is-device="isDevice"
            :data="users"
            @show-demo-options="showDemoOptions = $event"
          />
        </div>
      </v-card>
    </v-main>
  </div>
</template>

<script>
  import axios from 'axios'
  import { roomsRef, usersRef } from '@/firestore'
  import ChatContainer from './ChatContainer.vue'

  export default {
    components: {
      ChatContainer,
    },

    data () {
      return {
        theme: 'light',
        showChat: true,
        users: [],
        isDevice: false,
        showDemoOptions: true,
        updatingData: false,
        currentUserId: null,
      }
    },

    computed: {
      showOptions () {
        return !this.isDevice || this.showDemoOptions
      },
    },

    watch: {
      currentUserId () {
        this.showChat = false
        setTimeout(() => (this.showChat = true), 150)
      },
    },

    mounted () {
      this.isDevice = window.innerWidth < 500
      window.addEventListener('resize', ev => {
        if (ev.isTrusted) this.isDevice = window.innerWidth < 500
      })
      this.getData()
      // this.createRoom()
    },

    methods: {
      resetData () {
        roomsRef.get().then(val => {
          val.forEach(async val => {
            const ref = roomsRef.doc(val.id).collection('messages')

            await ref.get().then(res => {
              if (res.empty) return
              res.docs.map(doc => ref.doc(doc.id).delete())
            })

            roomsRef.doc(val.id).delete()
          })
        })
        usersRef.get().then(val => {
          val.forEach(val => {
            usersRef.doc(val.id).delete()
          })
        })
      },

      async createRoom () {
        this.disableForm = true

        // const { id } = await usersRef.add({ username: this.addRoomUsername })
        // await usersRef.doc(id).update({ _id: id })
        await roomsRef.add({
          users: ['2', this.currentUserId],
          lastUpdated: new Date(),
        })

        this.loadOnce()
      },

      getData () {
        this.currentUserId = this.$store.state.users.id.toString()
        const u = []
        axios.get('customer').then(res => {
          const d = res.data.data
          d.forEach(item => {
            u.push({
              _id: item.id,
              username: item.name,
              avatar: this.$baseUrl + item.photo,
            })
          })

          this.users = u
          this.addData()
        })
      },
      async addData () {
        this.updatingData = true
        for (let index = 0; index < this.users.length; index++) {
          const user = this.users[index]
          await usersRef.doc(user._id).set(user)
        }

        // await roomsRef.add({
        //   users: [user1._id, user2._id],
        //   lastUpdated: new Date(),
        // })
        // await roomsRef.add({
        //   users: [user1._id, user3._id],
        //   lastUpdated: new Date(),
        // })
        // await roomsRef.add({
        //   users: [user2._id, user3._id],
        //   lastUpdated: new Date(),
        // })
        // await roomsRef.add({
        //   users: [user1._id, user2._id, user3._id],
        //   lastUpdated: new Date(),
        // })

        this.updatingData = false
      },
      logout () {
        axios.get('auth/logout').then(() => {
          this.$store.commit('authentication', null)
          this.$store.commit('Authorization', false)
          this.$store.commit('users', {})
          this.$router.push('/')
        })
      },

      loadOnce: function () {
        location.reload()
      },

    },
  }
</script>

<style lang="scss">
body {
  background: #fafafa;
  margin: 0;
}

input {
  -webkit-appearance: none;
}

.app-container {
  font-family: 'Quicksand', sans-serif;
  padding: 20px 30px 30px;
}

.app-mobile {
  padding: 0;

  &.app-mobile-dark {
    background: #131415;
  }

  .user-logged {
    margin: 10px 5px 0 10px;
  }
  select {
    margin: 10px 0;
  }
  .button-theme {
    margin: 10px 10px 0 0;

    .button-github {
      height: 23px;

      img {
        height: 23px;
      }
    }
  }
}

.user-logged {
  font-size: 12px;
  margin-right: 5px;
  margin-top: 10px;

  &.user-logged-dark {
    color: #fff;
  }
}

select {
  height: 20px;
  outline: none;
  border: 1px solid #e0e2e4;
  border-radius: 4px;
  background: #fff;
  margin-bottom: 20px;
}

.button-theme {
  float: right;
  display: flex;
  align-items: center;

  .button-light {
    background: #fff;
    border: 1px solid #46484e;
    color: #46484e;
  }

  .button-dark {
    background: #1c1d21;
    border: 1px solid #1c1d21;
  }

  button {
    color: #fff;
    outline: none;
    cursor: pointer;
    border-radius: 4px;
    padding: 6px 12px;
    margin-left: 10px;
    border: none;
    font-size: 14px;
    transition: 0.3s;
    vertical-align: middle;

    &.button-github {
      height: 30px;
      background: none;
      padding: 0;
      margin-left: 20px;

      img {
        height: 30px;
      }
    }

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    @media only screen and (max-width: 768px) {
      padding: 3px 6px;
      font-size: 13px;
    }
  }
}

.version-container {
  padding-top: 20px;
  text-align: right;
  font-size: 14px;
  color: grey;
}
</style>
