<template>
  <div>
    <v-app-bar
      color="white"
      flat
    >
      <v-app-bar-nav-icon
        class="hidden-md-and-up"
        @click.stop="drawer = !drawer"
      />
      <v-container class="py-0 fill-height">
        <v-avatar
          width="100"
        >
          <v-img
            contain
            width="200"
            :src="require('@/assets/img/logo.png')"
          />
        </v-avatar>
        <v-btn
          to="/"
          text
          class="hidden-sm-and-down"
        >
          HOME
        </v-btn>
        <v-btn
          text
          class="hidden-sm-and-down"
          to="customer-page"
        >
          Orderan Saya
        </v-btn>
        <v-btn
          text
          to="/chat"
          class="hidden-sm-and-down"
        >
          Pesan
        </v-btn>
        <v-btn
          text
          class="hidden-sm-and-down"
          to="/profile"
        >
          Profil
        </v-btn>
        <v-btn
          text
          class="hidden-sm-and-down"
          @click="logout"
        >
          logout
        </v-btn>

        <v-spacer />
      </v-container>
    </v-app-bar>
    <v-main class="grey lighten-3">
      <v-container>
        <v-form>
          <v-row>
            <v-col
              cols="12"
              sm="12"
              md="2"
            >
              <v-sheet class="hidden-sm-and-down">
                <v-tabs
                  v-model="tab_menu"
                  class="ma-1"
                  vertical
                  color="blue"
                >
                  <v-tab href="#profile">
                    Profil
                  </v-tab>
                  <v-tab
                    href="#changePassword"
                  >
                    Ganti Password
                  </v-tab>
                </v-tabs>
              </v-sheet>
              <v-sheet class="hidden-md-and-up">
                <v-tabs
                  v-model="tab_menu"
                  class="ma-1"
                  color="blue"
                >
                  <v-tab href="#profile">
                    Profile
                  </v-tab>
                  <v-tab
                    href="#changePassword"
                  >
                    Change Password
                  </v-tab>
                </v-tabs>
              </v-sheet>
            </v-col>

            <v-col
              cols="12"
              sm="12"
              md="10"
              class="px-9"
            >
              <v-tabs-items v-model="tab_menu">
                <v-tab-item
                  id="profile"
                  class="ma-3"
                >
                  <v-row>
                    <v-col md="6">
                      <v-text-field
                        v-model="name"
                        :error-messages="nameErrors"
                        label="Nama"
                        required
                        outlined
                        @input="$v.name.$touch()"
                        @blur="$v.name.$touch()"
                      />
                      <v-text-field
                        v-model="email"
                        :error-messages="emailErrors"
                        label="E-mail"
                        required
                        outlined
                        @input="$v.email.$touch()"
                        @blur="$v.email.$touch()"
                      />
                      <v-text-field
                        v-model="phone"
                        :error-messages="phoneErrors"
                        label="phone"
                        required
                        outlined
                        @input="$v.phone.$touch()"
                        @blur="$v.phone.$touch()"
                      />
                      <v-text-field
                        v-model="phone2"
                        label="phone2 (Terhubung dengan whatsapp)"
                      />
                      <!-- <v-text-field
                        v-model="password"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        :error-messages="passwordErrors"
                        label="Password"
                        required
                        @input="$v.password.$touch()"
                        @blur="$v.password.$touch()"
                        @click:append="show1 = !show1"
                      /> -->
                      <v-select
                        v-model="city_id"
                        :items="this.$store.state.city"
                        item-text="nama"
                        item-value="city_id"
                        label="Kota"
                        required
                        outlined
                        @change="filterDistrictUser()"
                      />
                      <v-select
                        v-model="district_id"
                        :items="filteredDistrict"
                        item-text="nama"
                        item-value="id"
                        label="Kecamatan"
                        :disabled="city_id === null"
                        required
                        outlined
                        @change="filterVillageUser"
                      />
                      <v-select
                        v-if="district_id !== null"
                        v-model="village_id"
                        :items="filteredVillage"
                        item-text="nama"
                        item-value="id"
                        label="Kelurahan/Desa"
                        required
                        outlined
                      />

                      <v-select
                        v-else
                        v-model="village_id"
                        disabled
                        outlined
                        :items="filteredVillage"
                        item-text="nama"
                        item-value="id"
                        label="Kelurahan/Desa"
                      />
                    </v-col>
                    <v-col
                      md="6"
                    >
                      <v-textarea
                        v-model="address"
                        label="Alamat"
                        outlined
                        hint="Masukan Alamat"
                      />
                      <v-file-input
                        v-model="photo"
                        accept="image/jpeg"
                        prepend-icon=""
                        label="Gambar"
                        outlined
                        @change="preview($event)"
                      />
                      <div v-if="imgUrl">
                        <v-img
                          class="preview"
                          :src="$baseUrl+imgUrl"
                          width="30%"
                        />
                      </div>
                      <v-btn
                        class="float-right mr-2"
                        color="primary"
                        @click="updateUser()"
                      >
                        Simpan
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-tab-item>
                <v-tab-item id="changePassword">
                  <v-row class="ma-3">
                    <v-col md="6">
                      <v-text-field
                        v-model="password"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        :error-messages="passwordErrors"
                        label="Password"
                        required
                        outlined
                        @input="$v.password.$touch()"
                        @blur="$v.password.$touch()"
                        @click:append="show1 = !show1"
                      />
                      <v-text-field
                        v-model="passwordConfirmation"
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        :error-messages="passwordConfirmationErrors"
                        label="Password Confirmation"
                        required
                        outlined
                        @input="$v.passwordConfirmation.$touch()"
                        @blur="$v.passwordConfirmation.$touch()"
                        @click:append="show2 = !show2"
                      />

                      <v-btn
                        color="primary"
                        @click="changePassword()"
                      >
                        Simpan
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-tab-item>
              </v-tabs-items>
            </v-col>
          </v-row>
        </v-form>
      </v-container>
    </v-main>
  </div>
</template>
<script>
  import axios from 'axios'
  import { validationMixin } from 'vuelidate'
  import {
    required,
    maxLength,
    email,
    minLength,
    sameAs,
  } from 'vuelidate/lib/validators'
  export default {
    mixins: [validationMixin],
    validations: {
      name: { required, maxLength: maxLength(100) },
      email: { required, email },
      password: { required, minLength: minLength(8) },
      role: { required },
      passwordConfirmation: {
        required,
        sameAsPassword: sameAs('password'),
        minLength: minLength(8),
      },
      form: {
        weight: { required },
        selectedMethod: { required },
      },
      phone: { required, minLength: minLength(10) },
      category: { required },

    },
    data () {
      return {
        // rules: [
        //   value =>
        //     !value ||
        //     value.size < 10000000 ||
        //     'Avatar size should be less than 2 MB!',
        // ],
        show1: false,
        show2: false,
        name: null,
        email: null,
        phone: null,
        phone2: null,
        address: null,
        password: null,
        passwordConfirmation: null,
        tab_menu: 0,
        role_id: 5,
        city_id: null,
        village_id: null,
        district_id: null,
        city: [],
        district: [],
        village: [],
        filteredVillage: [],
        filteredDistrict: [],
        photo: null,
        imgUrl: null,
      }
    },

    computed: {
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.maxLength &&
          errors.push('Name must be at most 30 characters long')
        !this.$v.name.required && errors.push('Name is required.')
        return errors
      },
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.email && errors.push('Must be valid e-mail')
        !this.$v.email.required && errors.push('E-mail is required')
        return errors
      },
      passwordErrors () {
        const errors = []
        if (!this.$v.password.$dirty) return errors
        !this.$v.password.minLength && errors.push('At Least 8 Characters')
        !this.$v.password.required && errors.push('Password is required')
        return errors
      },

      passwordConfirmationErrors () {
        const errors = []
        if (!this.$v.passwordConfirmation.$dirty) return errors
        !this.$v.passwordConfirmation.sameAsPassword &&
          errors.push('Passwords must be identical')
        !this.$v.passwordConfirmation.required &&
          errors.push('Password Confirmation is required')
        return errors
      },
      phoneErrors () {
        const errors = []
        if (!this.$v.phone.$dirty) return errors
        !this.$v.phone.minLength &&
          errors.push('Phone must be at most 12 characters long')
        !this.$v.phone.required && errors.push('phone is required.')
        return errors
      },
    },

    mounted () {
      this.cond()
    },

    methods: {
      cond () {
        if (this.$store.state.authentication !== null) {
          const u = this.$store.state.users
          this.name = u.name
          this.email = u.email
          this.phone = u.phone
          this.phone2 = u.phone2
          this.photo = u.photo
          this.imgUrl = u.photo
          this.address = u.address
          this.city_id = u.city
          this.district_id = u.district
          this.village_id = u.village
          this.filterDistrictUser()
          this.filterVillageUser()
        }
      },

      updateUser () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('id', this.$store.state.users.id)
        form.append('name', this.name)
        form.append('email', this.email)
        form.append('address', this.address)
        form.append('role_id', this.role_id)
        form.append('phone', this.phone)
        form.append('phone2', this.phone2)
        form.append('photo', this.photo)
        form.append('city_id', this.city_id)
        form.append('district_id', this.district_id)
        form.append('village_id', this.village_id)
        axios.post('auth/update', form).then(() => {
          this.$swal('Data Updated Successfully', '', 'success')
          this.getUsers()
        })
      },

      filterDistrictUser () {
        // console.log(this.$store.state.village)
        this.filteredDistrict = []
        this.filteredDistrict = this.$store.state.district.filter(
          district => district.kabupaten_id === this.city_id,
        )
      },

      filterVillageUser () {
        // console.log(this.$store.state.village)
        this.filteredVillage = []
        this.filteredVillage = this.$store.state.village.filter(
          village => village.kecamatan_id === this.district_id,
        )
      },

      getUsers () {
        axios
          .get('customer/user')
          .then(res => {
            this.$store.commit('users', res.data.data)
          })
          .catch(error => {
            console.log(error)
          })
      },

      changePassword () {
        axios.post('auth/change-password', {
          password: this.password,
          email: this.$store.state.users.email,
        }).then(() => {
          this.$swal('Password has been changed', '', 'success')
        })
      },

      logout () {
        axios.get('auth/logout').then(() => {
          this.$store.commit('authentication', null)
          this.$store.commit('Authorization', false)
          this.$store.commit('users', {})
          this.$router.push('/')
        })
      },
    },
  }
</script>
