<template>
  <v-container>
    <v-dialog
      v-model="this.$store.state.dialog"
      max-width="500"
      persistent
    >
      <v-card
        class="pa-8"
        align-item="center"
        style="opacity:0.9"
        flat
      >
        <v-card-title class="pa-0">
          <h3>Masuk </h3>
          <v-spacer />
          <v-btn
            icon
            small
            @click="$store.commit('dialog',false)"
          >
            <v-icon>
              mdi-close
            </v-icon>
          </v-btn>
        </v-card-title>
        <v-form
          ref="form"
          v-model="valid"
          style="color:white;"
          lazy-validation
        >
          <h3>Sign In</h3>
          <v-divider />
          <v-text-field
            v-model="email"
            :rules="emailRules"
            :counter="10"
            label="E-mail"
            required
          />
          <v-text-field
            v-model="password"
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="passwordrules"
            :type="show1 ? 'text' : 'password'"
            name="input-10-1"
            label="Password"
            hint="At least 8 characters"
            counter
            @click:append="show1 = !show1"
          />
          <v-btn
            small
            :loading="loading"
            :disabled="loading"
            color="green accent-3 "
            class="mr-4"
            @click="signIn"
          >
            Masuk
          </v-btn>

          <v-btn
            small
            color="cyan accent-4"
            class="mr-4"
            @click="openForm"
          >
            Belum Punya Akun
          </v-btn>

          <v-btn
            x-small
            color="red"
            class="float-right mt-2"
            text
            @click="dialog = true"
          >
            lupa password ?
          </v-btn>
        </v-form>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="dialog"
      max-width="500"
    >
      <v-card
        class="pa-8"
        align-item="center"
        style="opacity:0.9"
        flat
      >
        <v-card-title class="pa-0">
          <h5>Email Konfirmasi </h5>
          <v-spacer />
          <v-btn
            icon
            small
            @click="dialog = false"
          >
            <v-icon>
              mdi-close
            </v-icon>
          </v-btn>
        </v-card-title>
        <v-text-field
          v-model="email"
          name="input-10-1"
          label="Email"
        />
        <p style="font-size:11px;color:red">
          *Pastikan email yang anda masukan benar
        </p>
        <v-card-action class="justify-content-right mb-3">
          <v-btn
            color="blue"
            class="float-right"
            small
            @click="sendEmail()"
          >
            kirim
          </v-btn>
        </v-card-action>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import axios from 'axios'

  export default {
    data () {
      return {
        show1: false,
        valid: true,
        loading: false,
        dialog: false,
        email: '',
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
        ],
        password: '',
        passwordrules: [
          v => !!v || 'Password is required',
          v => (v && v.length <= 12) || 'Password must be more than 6 characters',
        ],
        isLarge: this.$vuetify.breakpoint.lgAndUp,
        isSmall: this.$vuetify.breakpoint.mdOnly,
        isXSmall: this.$vuetify.breakpoint.smAndDown,
      }
    },
    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      signIn () {
        this.loading = true
        axios
          .post('/auth/login', {
            email: this.email,
            password: this.password,
          })
          .then(res => {
            // console.log(res.data);
            axios.defaults.headers.common.Authorization =
              'Bearer ' + res.data.token
            this.$store.commit('authentication', res.data.token)
            this.$store.commit('Authorization', true)
            this.$store.commit('users', res.data.users)
            this.loadOnce()
            this.loading = false
            this.$store.commit('dialog', false)
            // this.$store.commit('dialog', false)
          })
          .catch(() => {
            this.loading = false
            this.$swal('Email/Password Salah', '', 'error')
          })
      },

      openForm () {
        this.$store.commit('Authorization', true)
      },

      loadOnce: function () {
        location.reload()
      },

      sendEmail () {
        axios.post('/send', {
          email: this.email,
        }).then((res) => {
          this.$swal(res.data, '', 'success')
        })
      },
    },
  }
</script>
