// Vue
import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import secureLS from 'secure-ls'

// eslint-disable-next-line new-cap
var ls = new secureLS({
  encodingType: 'aes',
  encryptionSecret: '^77asj12@skdjasdk',
})
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    authentication: null,
    Authorization: false,
    users: {},
    city: [],
    district: [],
    village: [],
    methods: [],
    del_fee_list: [],
    dialog: false,
    dialog_customer: false,
    tour: true,
  },

  mutations: {
    authentication (state, val) {
      state.authentication = val
    },
    Authorization (state, val) {
      state.Authorization = val
    },
    users (state, val) {
      state.users = val
    },
    district (state, val) {
      state.district = val
    },
    village (state, val) {
      state.village = val
    },
    methods (state, val) {
      state.methods = val
    },
    del_fee_list (state, val) {
      state.del_fee_list = val
    },
    customer_list (state, val) {
      state.customer_list = val
    },
    dialog (state, val) {
      state.dialog = val
    },
    dialog_customer (state, val) {
      state.dialog_customer = val
    },
    tour (state, val) {
      state.tour = val
    },
    city (state, val) {
      state.city = val
    },
    },
    // eslint-disable-next-line no-dupe-keys
    plugins: [
      createPersistedState({
        storage: {
          getItem: (key) => ls.get(key),
          setItem: (key, value) => ls.set(key, value),
          removeItem: (key) => ls.remove(key),
        },
      }),
    ],
  })

export default store

