import Vue from 'vue'
import './plugins/base'
import vuetify from './plugins/vuetify'
import App from './App.vue'
import store from './store'
import router from './router'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import VueSweetalert2 from 'vue-sweetalert2'
import 'sweetalert2/dist/sweetalert2.min.css'
import './plugins/vuetify-money.js'
import VueFirestore from 'vue-firestore'
import VueTour from 'vue-tour'
import axios from 'axios'
var formatThousands = require('format-thousands')
require('vue-tour/dist/vue-tour.css')

Vue.prototype.$baseUrl = 'https://daengkurir.online/test/api/'
// Vue.prototype.$baseUrl = 'http://192.168.0.105:8000/'
Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(VueTour)
Vue.use(VueFirestore)
Vue.use(formatThousands)
Vue.use(VueSweetalert2)

if (store.state.Authorization) {
  axios.defaults.headers.common.Authorization =
    'Bearer' + store.state.authentication
  axios.defaults.baseURL = 'https://daengkurir.online/test/api/'
  // axios.defaults.baseURL = 'http://192.168.0.105:8000/'
  axios.defaults.headers['Content-Type'] =
    'Application/x-www-form-urlencoded;application/json'
  //  axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
} else {
  axios.defaults.baseURL = 'https://daengkurir.online/test/api/'
  // axios.defaults.baseURL = 'http://192.168.0.105:8000/'
  axios.defaults.headers['Content-Type'] =
    'Application/x-www-form-urlencoded;application/json'
  // axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
}

new Vue({
  beforeCreate: function () {
  },
  vuetify,
  store,
  router,
  render: h => h(App),
}).$mount('#app')
