                                        })
                                      }}
                                    </template>
                                    <template v-slot:item.price="{ item }">
                                      {{
                                        formatThousands(item.price, {
                                          separator: ',',
                                        })
                                      }}
                                    </template>
                                  </v-data-table>
                                </v-tab-item>
                              </v-tabs-items>
                            </v-card>
                          </v-card-text>
                        </v-card>
                      </v-tab-item>
                      <!-- tab express -->
                      <v-tab-item id="express">
                        <v-card
                          flat
                          class="mx-auto"
                        >
                          <v-card-text>
                            <v-card flat>
                              <v-tabs
                                v-model="tab"
                                class="ml-n10"
                                align-with-title
                                color="blue"
                              >
                                <v-tab href="#menu-1">
                                  <v-icon class="mr-1">
                                    mdi-cached
                                  </v-icon>
                                  Diproses
                                </v-tab>
                                <v-tab href="#menu-2">
                                  <v-icon class="mr-1">
                                    mdi-moped
                                  </v-icon>
                                  Dikirim
                                </v-tab>
                                <v-tab href="#menu-3">
                                  <v-icon class="mr-1">
                                    mdi-check
                                  </v-icon>
                                  Selesai
                                </v-tab>
                              </v-tabs>
                              <v-tabs-items v-model="tab">
                                <v-tab-item id="menu-1">
                                  <v-card-title>
                                    <v-spacer />
                                    <v-text-field
                                      v-model="search"
                                      append-icon="mdi-magnify"
                                      label="Search"
                                      single-line
                                      hide-details
                                    />
                                  </v-card-title>
                                  <v-data-table
                                    :headers="headers"
                                    :items="diprosesExp"
                                    :search="search"
                                  >
                                    <template v-slot:item.actions="{ item }">
                                      <v-tooltip top>
                                        <template
                                          v-slot:activator="{ on, attrs }"
                                        >
                                          <v-btn
                                            icon
                                            small
                                            v-bind="attrs"
                                            @click="dataDetailExp(item.id)"
                                            v-on="on"
                                          >
                                            <v-icon small>
                                              mdi-eye
                                            </v-icon>
                                          </v-btn>
                                        </template>
                                        <span>Detail</span>
                                      </v-tooltip>
                                      <v-tooltip top>
                                        <template
                                          v-slot:activator="{ on, attrs }"
                                        >
                                          <v-btn
                                            icon
                                            x-small
                                            v-bind="attrs"
                                            @click="confirm(item.id)"
                                            v-on="on"
                                          >
                                            <v-icon>mdi-close-circle</v-icon>
                                          </v-btn>
                                        </template>
                                        <span>Cancel Order</span>
                                      </v-tooltip>
                                    </template>
                                    <template
                                      v-slot:item.delivery_fee="{ item }"
                                    >
                                      {{
                                        formatThousands(item.delivery_fee, {
                                          separator: ',',
                                        })
                                      }}
                                    </template>
                                    <template v-slot:item.price="{ item }">
                                      {{
                                        formatThousands(item.price, {
                                          separator: ',',
                                        })
                                      }}
                                    </template>
                                  </v-data-table>
                                </v-tab-item>
                                <v-tab-item id="menu-2">
                                  <v-card-title>
                                    <v-spacer />
                                    <v-text-field
                                      v-model="search"
                                      append-icon="mdi-magnify"
                                      label="Search"
                                      single-line
                                      hide-details
                                    />
                                  </v-card-title>
                                  <v-data-table
                                    :headers="headers"
                                    :items="dikirimExp"
                                    :search="search"
                                  >
                                    <template v-slot:item.actions="{ item }">
                                      <v-btn
                                        icon
                                        small
                                      >
                                        <v-icon
                                          small
                                          class="mr-2"
                                          @click="dataDetailExp(item.id)"
                                        >
                                          mdi-eye
                                        </v-icon>
                                      </v-btn>
                                    </template>
                                    <template
                                      v-slot:item.delivery_fee="{ item }"
                                    >
                                      {{
                                        formatThousands(item.delivery_fee, {
