                                    </template>
                                  </v-data-table>
                                </v-tab-item>
                                <v-tab-item id="menu-3">
                                  <v-card-title>
                                    <v-spacer />
                                    <v-text-field
                                      v-model="search"
                                      append-icon="mdi-magnify"
                                      label="Search"
                                      single-line
                                      hide-details
                                    />
                                  </v-card-title>
                                  <v-data-table
                                    :headers="headers"
                                    :items="selesaiExp"
                                    :search="search"
                                  >
                                    <template v-slot:item.actions="{ item }">
                                      <v-btn
                                        icon
                                        small
                                      >
                                        <v-icon
                                          small
                                          class="mr-2"
                                          @click="dataDetailExp(item.id)"
                                        >
                                          mdi-eye
                                        </v-icon>
                                      </v-btn>
                                    </template>
                                    <template
                                      v-slot:item.delivery_fee="{ item }"
                                    >
                                      {{
                                        formatThousands(item.delivery_fee, {
                                          separator: ',',
                                        })
                                      }}
                                    </template>
                                    <template v-slot:item.price="{ item }">
                                      {{
                                        formatThousands(item.price, {
                                          separator: ',',
                                        })
                                      }}
                                    </template>
                                  </v-data-table>
                                </v-tab-item>
                              </v-tabs-items>
                            </v-card>
                          </v-card-text>
                        </v-card>
                      </v-tab-item>
                    </v-tabs-items>
                  </v-card>
                </v-container>
              </v-card>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
    <!-- Detail Dialog -->
    <v-dialog
      v-model="dialog"
      persistent
      width="1000"
    >
      <v-card>
        <v-card-title
          class="primary"
          style="color:white"
        >
          Detail Order
        </v-card-title>
        <v-card-text
          class="mt-6"
          style="color:black"
        >
          <v-row>
            <v-col>
              <div class="text-h6">
                Penerima
              </div>
              <table>
                <tr>
                  <td>Nama</td>
                  <td>:</td>
                  <td>{{ detail.receiver_name }}</td>
                </tr>
                <tr>
                  <td>No Hp</td>
                  <td>:</td>
                  <td>{{ detail.receiver_phone }}</td>
                </tr>
                <tr>
                  <td>Kecamatan</td>
                  <td>:</td>
                  <td>{{ detail.receiver_district }}</td>
                </tr>
                <tr>
                  <td>Alamat</td>
                  <td>:</td>
                  <td>{{ detail.desc_add }}</td>
                </tr>
              </table>
            </v-col>
            <v-col>
              <div class="text-h6">
                Pick Up Driver
              </div>
              <table>
                <tr>
                  <td>Nama</td>
                  <td>:</td>
                  <td>{{ detail.driver_name }}</td>
                </tr>
                <tr>
                  <td>No HP</td>
                  <td>:</td>
                  <td>{{ detail.driver_phone }}</td>
                </tr>
              </table>
            </v-col>
          </v-row>
          <div class="text-h6 mt-6">
            Produk
          </div>
          <v-data-table
            :headers="headers2"
            :items="product"
            :items-per-page="10"
          />
        </v-card-text>
        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog_confirm"
      persistent
      max-width="400"
    >
      <v-card>
        <v-card-title>
          Yakin ingin Batalkan Orderan ini
        </v-card-title>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="green darken-1"
            text
            @click="dialog_confirm = false"
          >
            Tidak
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="postStatus(6)"
          >
            Yakin
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  var formatThousands = require('format-thousands')
  export default {
    // eslint-disable-next-line vue/require-prop-types
    data () {
      return {
        selected: [],
        drawer: false,
        data: [],
        dialog: false,
        dialog_confirm: false,
        group: null,
        formatThousands,
        detail: [],
        product: [],
        exp: [],
        id: null,
        tab: 0,
        tab_cat: 0,
        search: '',
        isLarge: this.$vuetify.breakpoint.lgAndUp,
        isSmall: this.$vuetify.breakpoint.mdOnly,
        isXSmall: this.$vuetify.breakpoint.smAndDown,
        headers: [
          {
            text: 'No Order',
            align: 'start',
            value: 'no_order',
          },
          { text: 'Nama Produk', value: 'product_name' },
          { text: 'Nama Penerima', value: 'receiver_name' },
          { text: 'Harga', value: 'price' },
          { text: 'Ongkos Kirim', value: 'delivery_fee' },
          { text: 'Actions', value: 'actions', sortable: false },
        ],
        headers2: [
          {
            text: 'Nama',
            align: 'start',
            value: 'product_name',
          },
          { text: 'Berat', value: 'weight' },
          { text: 'Volume', value: 'volume' },
          { text: 'Harga', value: 'price' },
          { text: 'Ongkos Kirim', value: 'delivery_fee' },
          { text: 'Metode Pembayaran', value: 'method' },
        ],
      }
    },
    computed: {
      diproses () {
        const d = this.data.filter(
          item =>
            item.status === 'Assigned' ||
            item.status === 'Pickup Finish' ||
            item.status === 'Pickup Process',
        )
        return d
      },
      dikirim () {
        const d = this.data.filter(
          item =>
            item.status === 'Delivery Assigned' || item.status === 'Re-Deliver',
        )
        return d
      },
      selesai () {
        const d = this.data.filter(item => item.status === 'Delivered')
        return d
      },

      diprosesExp () {
        const d = this.exp.filter(
          item =>
            item.status === 'Delivery Assigned' && item.pickup_status === '0',
        )
        return d
      },
      dikirimExp () {
        const d = this.exp.filter(
          item =>
            item.status === 'Delivery Assigned' && item.pickup_status === '1',
        )
        return d
      },
      selesaiExp () {
        const d = this.exp.filter(item => item.status === 'Delivered')
        return d
      },
    },

    mounted () {
      this.getReg()
      this.getData()
    },
    methods: {
      getData () {
        axios.get('region').then((res) => {
          this.$store.commit('district', res.data.districts)
          this.$store.commit('village', res.data.village)
          this.$store.commit('city', res.data.special_city)
        })
      },

      getExp () {
        axios.get('customer/order-list-exp').then(res => {
          this.exp = res.data.data
        })
      },

      getReg () {
        axios.get('customer/order-list').then(res => {
          this.data = res.data
        })
      },

      postStatus (status) {
        axios
          .post('customer/cancel', {
            id: this.id,
            status: status,
          })
          .then(() => {
            this.refresh()
            this.dialog_confirm = false
          })
      },
      dataDetail (id) {
        const d = this.data.filter(item => item.id === id)
        this.detail = d[0]
        this.product = d
        this.dialog = true
      },

      dataDetailExp (id) {
        const d = this.exp.filter(item => item.id === id)
        this.detail = d[0]
        this.product = d
        this.dialog = true
      },
      confirm (id) {
        this.id = id
        this.dialog_confirm = true
      },

      refresh () {
        axios.get('customer/order-list').then(res => {
          this.data = res.data
        })
      },
      logout () {
        axios.get('auth/logout').then(() => {
          this.$store.commit('authentication', null)
          this.$store.commit('Authorization', false)
          this.$store.commit('users', {})
          this.$router.push('/')
        })
      },
    },
  }
</script>
